import * as React from 'react';
import Box from '@mui/material/Box';
import { Divider, Grid2 as Grid } from '@mui/material';
import "./NavBar.css"
import { DownloadOutlined, DownOutlined } from '@ant-design/icons';
import type { MenuProps } from 'antd';
import { Dropdown, Space } from 'antd';
import { Link, useNavigate } from "react-router-dom";

function NavBar() {

    const more: MenuProps['items'] = [
        {
            key: '1',
            label: (<Space>
                <DownloadOutlined /> Download Resume
            </Space>),
        },
        {
            key: '2',
            label: (<Space>
                <DownloadOutlined /> Download Portfolio
            </Space>),
        },
    ];

    const selectedWorks: MenuProps['items'] = [
        
        {
            key: '1',
            label: '2024',
            children: [
                {
                    key: '1-1',
                    label: 'Project 1',
                },
                {
                    key: '1-2',
                    label: 'Project 2',
                },
            ],
        },
        {
            key: '2',
            label: '2023',
            children: [
                {
                    key: '2-1',
                    label: 'Project 1',
                },
                {
                    key: '2-2',
                    label: 'Project 2',
                },
            ],
        },
        {
            key: '3',
            label: '2022',
            children: [
                {
                    key: '3-1',
                    label: 'Project 1',
                },
                {
                    key: '3-2',
                    label: 'Project 2',
                },
            ],
        },
        {
            type: 'divider',
        },
        {
            key: '4',
            label: 'View All',
          
        }

    ];


    return (

        <Box className='app-bar'>
            <Grid container sx={{ width: '100%' }}>
                <Grid size={2}></Grid>
                <Grid className='title-box' size={2} >
                    <p className='name'><b>NUTCHAYA<br />SAKULMANKONGSUK</b><br /><em>ARCHITECT</em></p>

                </Grid>
                <Grid size={3}></Grid>
                <Grid size={3} >
                    <Box className='nav-items' sx={{ display: 'flex' }}>
                        <p className='nav-item'>Home</p>
                        <Divider className='nav-divider' orientation="vertical" sx={{height: '25%'}} />
                        <Dropdown menu={{ items: selectedWorks }}>
                            <a> 
                                <Space>

                                    <Link className='nav-item' to="/selected-works">Selected Works</Link>
                                        
                                    <DownOutlined className='dropdown-arrow' />
                                </Space>
                            </a>
                        </Dropdown>
                        <Divider className='nav-divider' orientation="vertical" sx={{height: '25%'}} />
                        <p className='nav-item'>Contact</p>
                        <Divider className='nav-divider' orientation="vertical" sx={{height: '25%'}} />
                        <Dropdown menu={{ items: more }}>
                            <a>
                                <Space>
                                    <p className='nav-item'>More</p>
                                    <DownOutlined className='dropdown-arrow' />
                                </Space>
                            </a>
                        </Dropdown>
                    </Box>


                </Grid>
                <Grid size={2}></Grid>
            </Grid>


        </Box>

    );


};

export default NavBar;